/*!
 * Handsontable ContextMenu
 */

.htContextMenu:not(.htGhostTable) {
  display: none;
  position: absolute;
  /* needs to be higher than 1050 - z-index for Twitter Bootstrap modal (#1569) */
  z-index: 1060;
}

.htContextMenu .ht_clone_top,
.htContextMenu .ht_clone_bottom,
.htContextMenu .ht_clone_inline_start,
.htContextMenu .ht_clone_top_inline_start_corner,
.htContextMenu .ht_clone_bottom_inline_start_corner {
  display: none;
}
    

.htContextMenu .ht_master table.htCore  {
  border-color: #ccc;
  border-style: solid;
  border-top-width: 1px;
  border-bottom-width: 2px;
  border-left-width: 1px;
  border-right-width: 2px;
}
    [dir=rtl].htContextMenu .ht_master table.htCore  {
  border-right-width: 1px;
  border-left-width: 2px;}
  

.htContextMenu.handsontable:focus {
  outline: none;
}

.htContextMenu .wtBorder {
  visibility: hidden;
}

.htContextMenu table tbody tr td {
  background: white;
  border-width: 0;
  padding: 4px 6px 0 6px;
  cursor: pointer;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
    

.htContextMenu table tbody tr td:first-child  {
  border-top-width: 0;
  border-bottom-width: 0;
  border-left-width: 0;
  border-right-width: 0;
}
    [dir=rtl].htContextMenu table tbody tr td:first-child  {
  border-right-width: 0;
  border-left-width: 0;}
  

.htContextMenu table tbody tr td.htDimmed {
  font-style: normal;
  color: #323232;
}

.htContextMenu table tbody tr td.current {
  background: #f3f3f3;
}

.htContextMenu table tbody tr td.htSeparator {
  border-top: 1px solid #e6e6e6;
  height: 0;
  padding: 0;
  cursor: default;
}

.htContextMenu table tbody tr td.htDisabled {
  color: #999;
  cursor: default;
}

.htContextMenu table tbody tr td.htDisabled:hover {
  background: #fff;
  color: #999;
  cursor: default;
}

.htContextMenu table tbody tr.htHidden {
  display: none;
}
    

.htContextMenu table tbody tr td .htItemWrapper  {
  margin-left: 10px;
  margin-right: 6px;
}
    [dir=rtl].htContextMenu table tbody tr td .htItemWrapper  {
  margin-right: 10px;
  margin-left: 6px;}
  
    

.htContextMenu table tbody tr td div span.selected  {
  margin-top: -2px;
  position: absolute;
  left: 4px;
  right: 0;
}
    [dir=rtl].htContextMenu table tbody tr td div span.selected  {
  right: 4px;
  left: 0;}
  

.htContextMenu .ht_master .wtHolder {
  overflow: hidden;
}
